import { Component, OnInit } from '@angular/core';

import { DataService } from '@app/core';


@Component({
  selector: 'appc-two-factor-auth',
  template: require('./two-factor-auth.component.html'),
  styles: [require('./two-factor-auth.component.scss')]
})
export class TwoFactorAuthComponent implements OnInit {

  public enableAuthenticator: boolean;
  public model: ITwoFactorModel;
  constructor(private dataService: DataService) { }

  ngOnInit() {
    this.get2fModel();
  }

  private get2fModel() {
    this.dataService.post<ITwoFactorModel>('api/manage/twofactorauthentication')
      .subscribe(res => this.model = res);
  }
}



